	module.exports = {"pathContext":{"docHtml":"<p><a href=\"https://travis-ci.org/ReactiveX/rxjs\"><img src=\"https://travis-ci.org/ReactiveX/rxjs.svg?branch=master\" alt=\"Build Status\"></a>\n<a href=\"https://coveralls.io/github/ReactiveX/rxjs?branch=master\"><img src=\"https://coveralls.io/repos/github/ReactiveX/rxjs/badge.svg?branch=master\" alt=\"Coverage Status\"></a>\n<a href=\"http://badge.fury.io/js/%40reactivex%2Frxjs\"><img src=\"https://badge.fury.io/js/%40reactivex%2Frxjs.svg\" alt=\"npm version\"></a>\n<a href=\"https://gitter.im/Reactive-Extensions/RxJS?utm_source=badge&#x26;utm_medium=badge&#x26;utm_campaign=pr-badge&#x26;utm_content=badge\"><img src=\"https://badges.gitter.im/Join%20Chat.svg\" alt=\"Join the chat at https://gitter.im/Reactive-Extensions/RxJS\"></a></p>\n<p><a href=\"https://saucelabs.com/u/rxjs5\"><img src=\"https://saucelabs.com/browser-matrix/rxjs5.svg\" alt=\"Selenium Test Status\"></a></p>\n<h1>RxJS 5</h1>\n<p>Reactive Extensions Library for JavaScript. This is a rewrite of <a href=\"https://github.com/Reactive-Extensions/RxJS\">Reactive-Extensions/RxJS</a> and is the latest production-ready version of RxJS. This rewrite is meant to have better performance, better modularity, better debuggable call stacks, while staying mostly backwards compatible, with some breaking changes that reduce the API surface.</p>\n<p><a href=\"LICENSE.txt\">Apache 2.0 License</a></p>\n<ul>\n<li><a href=\"CODE_OF_CONDUCT.html\">Code of Conduct</a></li>\n<li><a href=\"CONTRIBUTING.html\">Contribution Guidelines</a></li>\n<li><a href=\"doc/maintainer-guidelines.html\">Maintainer Guidelines</a></li>\n<li><a href=\"doc/operator-creation.html\">Creating Operators</a></li>\n<li><a href=\"MIGRATION.html\">Migrating From RxJS 4 to RxJS 5</a></li>\n<li><a href=\"http://reactivex.io/rxjs\">API Documentation (WIP)</a></li>\n</ul>\n<h2>Versions In This Repository</h2>\n<ul>\n<li><a href=\"https://github.com/ReactiveX/rxjs/commits/master\">master</a> - commits that will be included in the next <em>minor</em> or <em>patch</em> release</li>\n<li><a href=\"https://github.com/ReactiveX/rxjs/commits/next\">next</a> - commits that will be included in the next <em>major</em> release (breaking changes)</li>\n</ul>\n<p>Most PRs should be made to <strong>master</strong>, unless you know it is a breaking change.</p>\n<h2>Important</h2>\n<p>By contributing or commenting on issues in this repository, whether you've read them or not, you're agreeing to the <a href=\"CODE_OF_CONDUCT.html\">Contributor Code of Conduct</a>. Much like traffic laws, ignorance doesn't grant you immunity.</p>\n<h2>Installation and Usage</h2>\n<h3>ES6 via npm</h3>\n<pre><code class=\"language-sh\">npm install rxjs\n</code></pre>\n<p>To import the entire core set of functionality:</p>\n<pre><code class=\"language-js\">import Rx from 'rxjs/Rx';\n\nRx.Observable.of(1,2,3)\n</code></pre>\n<p>To import only what you need by patching (this is useful for size-sensitive bundling):</p>\n<pre><code class=\"language-js\">import { Observable } from 'rxjs/Observable';\nimport 'rxjs/add/observable/of';\nimport 'rxjs/add/operator/map';\n\nObservable.of(1,2,3).map(x => x + '!!!'); // etc\n</code></pre>\n<p>To import what you need and use it with proposed <a href=\"https://github.com/tc39/proposal-bind-operator\">bind operator</a>:</p>\n<blockquote>\n<p>Note: This additional syntax requires <a href=\"http://babeljs.io/docs/plugins/transform-function-bind/\">transpiler support</a> and this syntax may be completely withdrawn from TC39 without notice! Use at your own risk.</p>\n</blockquote>\n<pre><code class=\"language-js\">import { Observable } from 'rxjs/Observable';\nimport { of } from 'rxjs/observable/of';\nimport { map } from 'rxjs/operator/map';\n\nObservable::of(1,2,3)::map(x => x + '!!!'); // etc\n</code></pre>\n<h3>CommonJS via npm</h3>\n<p>To install this library for CommonJS (CJS) usage, use the following command:</p>\n<pre><code class=\"language-sh\">npm install rxjs\n</code></pre>\n<p>Import all core functionality:</p>\n<pre><code class=\"language-js\">var Rx = require('rxjs/Rx');\n\nRx.Observable.of(1,2,3); // etc\n</code></pre>\n<p>Import only what you need and patch Observable (this is useful in size-sensitive bundling scenarios):</p>\n<pre><code class=\"language-js\">var Observable = require('rxjs/Observable').Observable;\n// patch Observable with appropriate methods\nrequire('rxjs/add/observable/of');\nrequire('rxjs/add/operator/map');\n\nObservable.of(1,2,3).map(function (x) { return x + '!!!'; }); // etc\n</code></pre>\n<p>Import operators and use them <em>manually</em> you can do the following (this is also useful for bundling):</p>\n<pre><code class=\"language-js\">var of = require('rxjs/observable/of').of;\nvar map = require('rxjs/operator/map').map;\n\nmap.call(of(1,2,3), function (x) { return x + '!!!'; });\n</code></pre>\n<p>You can also use the above method to build your own Observable and export it from your own module.</p>\n<h3>All Module Types (CJS/ES6/AMD/TypeScript) via npm</h3>\n<p>To install this library via <a href=\"https://www.npmjs.org\">npm</a> <strong>version 3</strong>, use the following command:</p>\n<pre><code class=\"language-sh\">npm install @reactivex/rxjs\n</code></pre>\n<p>This will include CJS/Global builds and can be used for all module types.</p>\n<p>If you are using npm <strong>version 2</strong> before this library has achieved a stable version, you need to specify the library version explicitly:</p>\n<pre><code class=\"language-sh\">npm install @reactivex/rxjs@5.0.0\n</code></pre>\n<h3>CDN</h3>\n<p>For CDN, you can use <a href=\"https://unpkg.com/\">unpkg</a>:</p>\n<p><a href=\"https://unpkg.com/rxjs/bundles/Rx.min.js\">https://unpkg.com/rxjs/bundles/Rx.min.js</a></p>\n<h4>Node.js Usage:</h4>\n<pre><code class=\"language-js\">var Rx = require('@reactivex/rxjs');\n\nRx.Observable.of('hello world')\n  .subscribe(function(x) { console.log(x); });\n</code></pre>\n<h2>Goals</h2>\n<ul>\n<li>Provide better performance than preceding versions of RxJS</li>\n<li>To model/follow the <a href=\"https://github.com/zenparsing/es-observable\">Observable Spec Proposal</a> to the observable.</li>\n<li>Provide more modular file structure in a variety of formats</li>\n<li>Provide more debuggable call stacks than preceding versions of RxJS</li>\n</ul>\n<h2>Building/Testing</h2>\n<p>The build and test structure is fairly primitive at the moment. There are various npm scripts that can be run:</p>\n<ul>\n<li>build_es6: Transpiles the TypeScript files from <code>src/</code> to <code>dist/es6</code></li>\n<li>build_cjs: Transpiles the ES6 files from <code>dist/es6</code> to <code>dist/cjs</code></li>\n<li>build_amd: Transpiles the ES6 files from <code>dist/es6</code> to <code>dist/amd</code></li>\n<li>build_global: Transpiles/Bundles the CommonJS files from <code>dist/cjs</code> to <code>dist/global/Rx.js</code></li>\n<li>build_all: Performs all of the above in the proper order.</li>\n<li>build_test: builds ES6, then CommonJS, then runs the tests with <code>jasmine</code></li>\n<li>build_perf: builds ES6, CommonJS, then global, then runs the performance tests with <code>protractor</code></li>\n<li>build_docs: generates API documentation from <code>dist/es6</code> to <code>dist/docs</code></li>\n<li>build_cover: runs <code>istanbul</code> code coverage against test cases</li>\n<li>test: runs tests with <code>jasmine</code>, must have built prior to running.</li>\n<li>tests2png: generates PNG marble diagrams from test cases.</li>\n</ul>\n<p><code>npm run info</code> will list available script.</p>\n<h3>Example</h3>\n<pre><code class=\"language-sh\"># build all the things!\nnpm run build_all\n</code></pre>\n<h2>Performance Tests</h2>\n<p>Run <code>npm run build_perf</code> or <code>npm run perf</code> to run the performance tests with <code>protractor</code>.\nRun <code>npm run perf_micro</code> to run micro performance test benchmarking operator.</p>\n<h2>Adding documentation</h2>\n<p>RxNext uses <a href=\"https://esdoc.org/\">ESDoc</a> to generate API documentation. Refer to ESDoc's documentation for syntax. Run <code>npm run build_docs</code> to generate.</p>\n<h2>Generating PNG marble diagrams</h2>\n<p>The script <code>npm run tests2png</code> requires some native packages installed locally: <code>imagemagick</code>, <code>graphicsmagick</code>, and <code>ghostscript</code>.</p>\n<p>For Mac OS X with <a href=\"http://brew.sh/\">Homebrew</a>:</p>\n<ul>\n<li><code>brew install imagemagick</code></li>\n<li><code>brew install graphicsmagick</code></li>\n<li><code>brew install ghostscript</code></li>\n<li>\n<p>You may need to install the Ghostscript fonts manually:</p>\n<ul>\n<li>Download the tarball from the <a href=\"https://sourceforge.net/projects/gs-fonts\">gs-fonts project</a></li>\n<li><code>mkdir -p /usr/local/share/ghostscript &#x26;&#x26; tar zxvf /path/to/ghostscript-fonts.tar.gz -C /usr/local/share/ghostscript</code></li>\n</ul>\n</li>\n</ul>\n<p>For Debian Linux:</p>\n<ul>\n<li><code>sudo add-apt-repository ppa:dhor/myway</code></li>\n<li><code>apt-get install imagemagick</code></li>\n<li><code>apt-get install graphicsmagick</code></li>\n<li><code>apt-get install ghostscript</code></li>\n</ul>\n<p>For Windows and other Operating Systems, check the download instructions here:</p>\n<ul>\n<li><a href=\"http://imagemagick.org\">http://imagemagick.org</a></li>\n<li><a href=\"http://www.graphicsmagick.org\">http://www.graphicsmagick.org</a></li>\n<li><a href=\"http://www.ghostscript.com/\">http://www.ghostscript.com/</a></li>\n</ul>","docPath":"argo-ci/node_modules/rxjs/_esm5/readme","proj":"argo-ci"}}

